
import React,{Component} from 'react';
class Nav extends Component{
    render(){
        return (
            <div className={this.props.name.hd_right}>
                <ul className={this.props.name.nav}>
                    <li><span></span> <i>&#xe600;</i></li>
                    <li><span></span> <i>&#xe600;</i></li>
                    <li><span></span> <i>&#xe600;</i></li>
                    <li><span></span> <i>&#xe600;</i></li>
                    <li><span></span> <i>&#xe600;</i></li>
                    <li><span></span> <i>&#xe600;</i></li>
                    <li><span></span> <i>&#xe600;</i></li>
                    <li><span></span> <i>&#xe600;</i></li>
                </ul>
                <ul className={this.props.name.hd_lang}>
                    <li>
                        English <span>&#xe604;</span>
                    </li>
                    <li>
                         <span>&#xe604;</span>  
                    </li>   
                </ul>
                <button>
                    &#xe634;
                </button>
            </div>
        )
    }
}
export default Nav;


