import React,{Component} from "react";
import Slider from 'react-slick';
import styles from './css/styles.css';

class SimpleSlider extends Component {
    render() {
        var settings = {
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            arrows:false,
            autoplay:true,
            dotsClass: `${styles.dots_wrap}`
        };
        return(
            <Slider {...settings }>
                <div className={styles.slide_wrap}>
                    <div className={styles.slide}>
                        <div>
                            <p>CERI</p>
                            <h3></h3>
                        </div>
                    </div>
                </div>
                <div>
                    <div className={styles.slide}>
                        <div>
                            <p>CERI</p>
                            <h3></h3>
                        </div>
                    </div>
                </div>
                <div>
                    <div className={styles.slide}>
                        <div>
                            <p>CERI</p>
                            <h3></h3>
                        </div>
                    </div>
                </div>
                <div>
                    <div className={styles.slide}>
                        <div>
                            <p>CERI</p>
                            <h3></h3>
                        </div>
                    </div>  
                </div>
            </Slider>
            
        );
    }
}
export default SimpleSlider;













