import React,{Component} from "react";
import styles from './css/css.css'
class Business extends Component{
    render(){
        return(
            <div className={styles.business}>
                <div className={this.props.name.concent+' '+styles.businessContent+' '+this.props.name.clearfix}>
                    <div className={styles.left+' '+styles.font}>
                        <h4>CERI</h4>
                        <p></p>
                    </div>
                    <ul>
                        <li>
                            <div>
                                <i>&#xe966;</i>
                            </div>
                            <p></p>
                        </li>
                        <li>
                            <div>
                                <i>&#xe6ea;</i>
                            </div>
                            <p></p>
                        </li>
                        <li>
                            <div>
                                <i>&#xe66f;</i>
                            </div>
                            <p></p>
                        </li>
                        <li>
                            <div>
                                <i>&#xe61f;</i>
                            </div>
                            <p></p>
                        </li>
                        <li>
                            <div>
                                <i>&#xe640;</i>
                            </div>
                            <p></p>
                        </li>
                    </ul>
                </div>
            </div>
        )
    }
}
export default Business


