
import React,{Component} from 'react';
import styles from './css/style.css';
import Nav from './Nav/Nav.js';
class NavMain extends Component {
    render(){
        return (
            <header>
                <div className={`${this.props.name.clearfix} ${styles.hd_wrap}`}>
                    <div className={styles.hd_left}><img src={require('./img/logo.jpg')} alt='logo'/></div>
                    <Nav name={styles} />
                </div>
            </header>
        )
    }
    

}
export default NavMain;


